import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { EurekaArrowUp } from '@Green-Dot-Corporation/eureka-ui-icons';
import { Conversation } from '@twilio/conversations';
import WindowContainer from '../window-container/WindowContainer';
import ChatTextBubble from './ChatTextBubble';
import ChatTypingBubble from './ChatTypingBubble';
import ChatImageBubble from './ChatImageBubble';
import MessageInput from '../message-input/MessageInput';
import Attach from '../svg/Attach';
// Styles
import './styles/chat-window.scss';
import TwilioService from '../../service/TwilioService';

const { PRIMARY, SECONDARY } = VARIANTS;
class ChatWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        i18n: PropTypes.object,
        email: PropTypes.string,
        msgMaxLength: PropTypes.number,
        lineHeight: PropTypes.number,
        onChatWidgetClose: PropTypes.func,
    };

    static defaultProps = {
        msgMaxLength: 280,
        lineHeight: 27,
    };

    state = {
        messages: [],
        shouldShowCounter: false,
        count: 0,
        isSendMessageBtnEnabled: false,
        shouldShowTyping: false,
        messageToSend: undefined,

        scrollToBottom: false,
    };

    render() {
        const { contextCls, i18n, email, ...restProps } = this.props;

        const { messages, shouldShowTyping } = this.state;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                <WindowContainer
                    contextCls={cx(`${this.baseCls}__chat-window-container`)}
                    title={i18n.getTranslation('chatWindow.title')}
                    shouldShowCloseBtn
                    shouldShowMinimizeBtn
                    onCloseBtnClick={this.handleCloseBtnClick}
                    onMinimizeBtnClick={this.handleMinimizeBtnClick}
                    {...restProps}
                    i18n={i18n}
                >
                    <div className={`${this.baseCls}__chat-text-container`}>
                        <div className={`${this.baseCls}__messages-container`}>
                            {messages.map((message) =>
                                message.type === 'media'
                                    ? this.renderImageBubble(message)
                                    : this.renderTextBubble(message),
                            )}
                            {shouldShowTyping && <ChatTypingBubble />}
                        </div>
                        <div
                            className={`${this.baseCls}__send-message-container`}
                        >
                            <MessageInput
                                contextCls={`${this.baseCls}__message-input`}
                                placeholder={i18n.getTranslation(
                                    'chatWindow.messageInputPlaceholder',
                                )}
                                aria-label={i18n.getTranslation(
                                    'chatWindow.messageInputAriaLabel',
                                )}
                                onResize={this.handleResize}
                                onChange={this.handleMessageChange}
                                onKeyDown={this.handleKeyDown}
                                renderAfterControl={this.renderAfter}
                            />
                        </div>
                    </div>
                </WindowContainer>
            </div>
        );
    }

    componentDidUpdate(prevProps, prevStates) {
        const { scrollToBottom } = this.state;

        if (prevStates.scrollToBottom === false && scrollToBottom === true) {
            this.scrollToBottom();
        }
    }

    componentDidMount() {
        this.handleConversationMessage();
        this.handleConversationEvents();
        this.loadMessages();
    }

    componentWillUnmount() {
        this.detachConversationEvents();
    }

    renderCounter = () => {
        const { msgMaxLength } = this.props;

        return (
            <div className={`${this.baseCls}__counter-container`}>
                <span>
                    {this.state.count}/{msgMaxLength}
                </span>
            </div>
        );
    };

    renderActions = () => {
        const { i18n } = this.props;
        const { isSendMessageBtnEnabled } = this.state;

        return (
            <div className={`${this.baseCls}__actions-container`}>
                <Button
                    contextCls={cx(`${this.baseCls}__attach-btn`)}
                    renderIcon={this.renderAttachIcon}
                    onClick={this.handleAttachBtnClick}
                    variant={SECONDARY}
                    aria-label={i18n.getTranslation(
                        'chatWindow.actionsContainer.attachBtnAriaLabel',
                    )}
                />
                <Button
                    contextCls={cx(`${this.baseCls}__send-btn`)}
                    renderIcon={this.renderArrowUpIcon}
                    onClick={this.handleSendBtnClick}
                    variant={PRIMARY}
                    aria-label={i18n.getTranslation(
                        'chatWindow.actionsContainer.sendBtnAriaLabel',
                    )}
                    isDisabled={!isSendMessageBtnEnabled}
                />
            </div>
        );
    };

    renderAttachIcon = () => {
        return <Attach />;
    };

    renderArrowUpIcon = () => {
        return <EurekaArrowUp />;
    };

    renderAfter = () => {
        const { shouldShowCounter } = this.state;

        return (
            <div className={`${this.baseCls}__after-control-container`}>
                {shouldShowCounter && this.renderCounter()}
                {this.renderActions()}
            </div>
        );
    };

    renderTextBubble = (m) => {
        const { email } = this.props;
        const isTextFromMe = m.author === email;

        if (isTextFromMe) {
            return <ChatTextBubble id={m.sid} key={m.sid} text={m.body} />;
        }

        return (
            <ChatTextBubble
                id={m.sid}
                key={m.sid}
                text={m.body}
                isTextFromMe={false}
            />
        );
    };

    renderImageBubble = (m) => {
        const { media } = m;
        const { contextCls, i18n, email, ...restProps } = this.props;
        const isTextFromMe = m.author === email;

        if (isTextFromMe) {
            return (
                <ChatImageBubble
                    key={m.sid}
                    media={media}
                    i18n={i18n}
                    {...restProps}
                />
            );
        }

        return (
            <ChatImageBubble
                key={m.sid}
                media={media}
                i18n={i18n}
                isFromOther
                {...restProps}
            />
        );
    };

    handleConversationEvents = async () => {
        const conversation = await TwilioService.getConversation();
        conversation.on(Conversation.messageAdded, this.handleMessageAdded);
        conversation.on(Conversation.typingStarted, this.handleTypingStarted);
        conversation.on(Conversation.typingEnded, this.handleTypingEnded);
    };

    handleConversationMessage = async () => {
        const conversation = await TwilioService.getConversation();
        const message = await conversation.getMessages();
        this.setState({
            messages: message.items,
        });
    };

    handleAttachBtnClick = () => {};

    handleSendBtnClick = () => {
        // TODO replace with sending via Twilio client
        console.log(`sending...${this.state.messageToSend}`);
    };

    handleResize = (e) => {
        const { lineHeight } = this.props;
        const rows = Math.floor(e.target.scrollHeight / lineHeight);

        if (rows > 1) {
            this.setState({ shouldShowCounter: true });
        } else {
            this.setState({ shouldShowCounter: false });
        }
    };

    handleMessageChange = (e) => {
        const currentText = e.target.value;
        const characterCount = currentText?.trim().replace(/\n/g, '').length;
        const hasMessage = e.target.value.trim().length > 0;
        this.setState({
            count: characterCount,
            isSendMessageBtnEnabled: hasMessage,
            messageToSend: e.target.value,
        });
        // TODO replace with sending via Twilio client
        console.log(this.state.messageToSend);
    };

    handleTypingStarted = () => {
        this.handleTyping(true);
    };

    handleTypingEnded = () => {
        this.handleTyping(false);
    };

    handleTyping = (isTyping) => {
        this.setState({
            shouldShowTyping: isTyping,
        });
    };

    handleKeyDown = (e) => {
        const { count } = this.state;

        if (e.keyCode === 13 && e.shiftKey === false) {
            e.preventDefault();

            if (count > 0) {
                this.handleSendBtnClick();
            }
        }
    };

    handleCloseBtnClick = () => {
        const { onChatWidgetClose } = this.props;
        onChatWidgetClose();
    };

    handleMinimizeBtnClick = () => {
        // TODO: will replace by minimize the window
        console.log('min btn click');
    };

    handleMessageAdded = (message) => {
        const { messages } = this.state;
        this.setState({
            messages: [...messages, message],
            scrollToBottom: true,
        });
    };

    detachConversationEvents = async () => {
        const conversation = await TwilioService.getConversation();
        conversation.off(Conversation.messageAdded, this.handleMessageAdded);
        conversation.off(Conversation.typingStarted, this.handleTypingStarted);
        conversation.off(Conversation.typingEnded, this.handleTypingEnded);
    };

    scrollToBottom = () => {
        const messageContainer = document.getElementsByClassName(
            `${this.baseCls}__messages-container`,
        )[0];
        messageContainer.scrollTop = messageContainer.scrollHeight;
        this.setState({ scrollToBottom: false });
    };

    loadMessages = async () => {
        const conversation = await TwilioService.getConversation();

        conversation
            .getMessages()
            .then((m) => {
                this.setState({ messages: m.items, scrollToBottom: true });
            })
            .catch((err) => {
                console.error("Couldn't fetch messages IMPLEMENT RETRY", err);
            });

        conversation.on(Conversation.messageAdded, this.handleMessageAdded);
    };

    baseCls = 'web-chat-window';
}

export default ChatWindow;
